body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.content{
  height: 80vh;
}
.Register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}
/* transections page */

.filters{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}
/* transections page */

.anticon svg {
  font-size: 20px;
  cursor:pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.switch-icons{
  border:1px solid rgba(0, 0, 0, 0.639);
  border-radius: 5px;
  padding: 10px 15px;
}